.cate {
  width: 100%;
  height: 100vh;
  position: relative;
  top: 0;
  left: 0;
  z-index: 1;
  padding: 0.16rem;
}
.cate-body {
  width: 80%;
  height: 10.666667rem;
  background-color: #d9dae0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-radius: 0.16rem;
}
.cate-body-list {
  width: 40%;
  height: 2.666667rem;
  background-color: #2c8ee3;
  font-size: 0.266667rem;
  border-radius: 0.16rem;
}
.cate-list-href {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  top: 0;
  left: 0;
  color: #fff;
}
.cate-body-list:hover {
  background-color: #4ea8f5;
}
.cate-list-text {
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding: 0 0.16rem;
  font-size: 0.373333rem;
}
.homeBody {
  width: 100%;
  height: 100%;
}
.headBody {
  width: 100%;
}
.headBody-h3 {
  /*font-size: 40;*/
  width: 100%;
  text-align: center;
  padding: 0.08rem 0;
}
.headBody-title {
  width: 100%;
  font-size: 0.4rem;
  padding: 0.08rem;
  overflow: hidden;
}
.headBody-title > li {
  width: 50%;
  /*line-height: 20px;*/
  text-indent: 0.08rem;
  float: left;
  padding: 0.053333rem 0;
}
.tableBody {
  width: 100%;
  /*height: 88%;*/
  padding: 0.32rem 0.16rem;
  font-size: 0.213333rem;
  overflow: hidden;
}
.tableBody-table {
  width: 100%;
  padding: 0.08rem 0.16rem;
  font-size: 0.213333rem;
}
.tableBody-tr {
  width: 100%;
  border: 0.013333rem solid #000;
  font-size: 0.266667rem;
  text-align: center;
}
.vertical {
  vertical: center;
}
tr > th {
  max-width: 0.8rem;
  padding: 0.053333rem 0;
  font-size: 0.4rem;
}
tr > td {
  padding: 0.013333rem 0;
  max-width: 0.8rem;
  word-wrap: break-word;
  font-size: 0.373333rem;
  text-align: center;
}
.td-text-center {
  text-align: center;
}
.td-input {
  width: 100%;
  height: 100%;
  text-align: center;
  border: none;
  outline: #2c8ee3;
}

/*  按钮 */
.btn {
  width: 100%;
  padding: 0.16rem;
}
.btn > .btn-submit {
  outline: none;
  border: none;
  background-image: -webkit-linear-gradient(93deg,#3f51b5,#00bbd4);
  color: #fff;
  background: #1d89ff;
  font-size: 0.346667rem;
  font-family: inherit;
  padding: 0.066667rem 0.16rem;
  letter-spacing: 0.266667rem;
  text-indent: 0.133333rem;
  border-radius: 0.08rem;
  float: right;
}@charset "UTF-8";

